.container {
  padding: 2vw;
  border: 1px solid #ccc;
  border-radius: 5px;
}

input[type="text"] {
  padding: 0.5rem 1rem;
  color: transparent;
  text-shadow: 0 0 0 #2196f3;
  border: none;
  outline: none;
  background-color: #f2f2f2;
  border-radius: 3px;
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
  width: 100%;
}

@media (min-width: 768px) {
  textarea{
    columns : 1;
  }
  input[type="text"] {
    width: 80%;
  }
}

#Tulathumb {
  cursor: pointer;
  color: black;
}

button {
  padding: 0.5rem 1rem;
  background-color: #2196f3;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
}

button:hover {
  background-color: #0c7cd5;
}

.custom-alert {
  position: fixed;
  top: 2vw;
  right: 2vw;
  background-color: #4caf50;
  color: #fff;
  border-radius: 4px;
  padding: 1rem;
  display: flex;
  align-items: center;
  animation: custom-alert-animation 0.5s ease-in-out;
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
}

.custom-alert i {
  margin-right: 1rem;
}

.custom-alert-text {
  font-weight: bold;
  color: #fff;
}

@keyframes custom-alert-animation {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
h3{
  color: red;
}
textarea {
  caret-color: transparent;
  width: 100%;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  background-color: #f2f2f2;
  border: none;
  outline: none;
  border-radius: 3px;
  font-family: 'Open Sans', sans-serif;
}
